import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import * as styled from './navbar.styled';


const Navbar = (props) => {

    const [hamburger, setHamburger] = useState(false);


    // NAVBAR MAGIC!!
    const [navbarMagic, setNavbarMagic] = useState(false);
    const [scrollPoint, setScrollPoint] = useState(window.scrollY)

    const handleScroll = () => {
        setScrollPoint(window.scrollY);
    }

    useEffect( () => {
        if (scrollPoint >= 45) {setNavbarMagic(true)} else {setNavbarMagic(false)}
    }, [scrollPoint]);

    useEffect( () => {
        window.addEventListener('scroll', handleScroll);
    }, [])

    
    

    return (
        <styled.navbar status={ hamburger } navbar_magic={navbarMagic}>
            <styled.navbar_moving_container status={ hamburger } navbar_magic={navbarMagic}>
                <styled.logo_container1>
                    <styled.logo_container status={ hamburger } navbar_magic={navbarMagic}>
                        <styled.logo status={ hamburger } navbar_magic={navbarMagic}>Affiliatet.se </styled.logo>
                    </styled.logo_container>
                </styled.logo_container1>
                <styled.navbar_items_container status={ hamburger } navbar_magic={navbarMagic}>
                    <Link to={``}><styled.navbar_items status={ hamburger }>Hem</styled.navbar_items></Link>
                    <Link to={`verktyg`}><styled.navbar_items status={ hamburger }>Verktyg</styled.navbar_items></Link>
                    <Link to={`nätverk`}><styled.navbar_items status={ hamburger }>Affiliate Nätverk</styled.navbar_items></Link>
                    <Link to={`program`}><styled.navbar_items status={ hamburger }>Affiliate Program</styled.navbar_items></Link>
                </styled.navbar_items_container>
                <styled.navbar_toggler status={ hamburger }  onClick={ () => setHamburger(!hamburger) }>
                    <styled.navbar_burger navbar_magic={navbarMagic} status={ hamburger } />
                </styled.navbar_toggler>
            </styled.navbar_moving_container>
        </styled.navbar>
    );
}

export default Navbar;