import styled from '@emotion/styled';


export const network = styled.div`
    width: 100%;
    height: 200vh;
    

`
export const network_container = styled.div`
    padding-top: 5vh;
    height: 100%;
    width: 100%;

`

export const filter_container1 = styled.div`
    height: 20vh;
    width: 100%;

`
export const filter_container2 = styled.div`
    
    align-items: center;
    display: grid;
    justify-content: center;
    width: 65vw;
    margin: 0 auto;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr ;
    grid-gap: 10px;

    
    
`
export const filter_container3 = styled.button`
    background-color: #fff;
    box-shadow: 0px 2px 2px 1px rgba(13, 2, 18, 0.05);
    border-radius: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    width: 100%;
    outline: 0;
    transition: all .2s ease-in-out;
    border-style: none;

    :hover {
          
       
    }
    :focus {
        background-color: #3B49DF;
        color: #fff;
        outline: 0;
    }
    
`

export const topic = styled.h2`
    font-family: 'Open Sans Condensed', sans-serif;
`
export const cardview_container = styled.div`
    margin-top: 10vh;
    display: grid;
    
    grid-template-rows: 300px;
`
export const cardview = styled.div`
    display: grid;
    grid-gap: 10px;
    
`
export const card_view2 = styled.div`
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 20px;
    margin: 0 120px;
    box-shadow: 0px 2px 2px 1px rgba(13, 2, 18, 0.05);
`
export const img_view = styled.img`
    height: 200px;
    width: 200px;
    border-radius: 50px;
   
`
export const mid_container = styled.div`
    
    
`
export const title_text = styled.h1`
    font-family: 'Open Sans Condensed', sans-serif;
`
export const program_text = styled.h3`
    font-family: 'Open Sans', sans-serif;
`

export const submit_button = styled.button`
    margin-right: 50px;
    width: 100px;
    height: 50px;
    color: white;
    border-radius: 5px;
    background-color: #15BE80;
    border-style: none;
    box-shadow: 0px 2px 2px 1px rgba(13, 2, 18, 0.05);
    font-family: 'Open Sans', sans-serif;
    
    :focus {
        outline:0;
    }
    
    :hover {
        transition: all .2s ease-in-out;
    }
    
`