import * as styled from './page_container.styled';
import React from 'react';
import { Route } from 'react-router-dom'

import Home_page from '../pages/affiliate_home_page/home.component.jsx'
import Tools_page from '../pages/affiliate_tools_page/tools.component.jsx'
import Networks_page from '../pages/affiliate_networks_page/networks.component.jsx'
import Programs_page from '../pages/affiliate_programs_page/programs.component.jsx'

const Page_Container = (props) => {
    return(
        <styled.page_container>
            <Route path={'/'} exact> <Home_page /> </Route>
            <Route path={'/verktyg'} exact> <Tools_page /> </Route>
            <Route path={'/nätverk'} exact> <Networks_page categories={props.categories} affiliate_networks={props.affiliate_networks}/> </Route>
            <Route path={'/program'} exact> <Programs_page /> </Route>
        </styled.page_container>
    );
}          

export default Page_Container;