import React from 'react';
import * as styled from './network.styled';


const Network = (props) => {


    return (
        <styled.network>
            <styled.network_container>
                <styled.filter_container1>
                    <styled.filter_container2>
                        {props.categories.map( item => {
                            return(
                                <styled.filter_container3>
                                    <styled.topic>{item}</styled.topic>
                                </styled.filter_container3>
                            );
                        })}
                    </styled.filter_container2>
                    <styled.cardview_container>
                        <styled.cardview>
                            {props.affiliate_networks.map( (element, index) => {
                            
                                return (
                                <styled.card_view2>
                                    <styled.img_view src={element.network.logo}/>
                                    {console.log(element.network.logo)}
                                    <styled.mid_container>
                                        <styled.title_text>{element.network.name}</styled.title_text>
                                        <styled.program_text>{`Antal program: ${element.network.program}`}</styled.program_text>
                                    </styled.mid_container>
                                    <styled.submit_button>Registera!</styled.submit_button>
                                </styled.card_view2>    
                                );
                            })}
                        </styled.cardview>
                    </styled.cardview_container>
                </styled.filter_container1>
            </styled.network_container>
        </styled.network>

    )
}

export default Network;
