import styled from '@emotion/styled';


// -------- NAVBAR ----------------
export const navbar = styled.div`
    position: relative;
    height: 70px;
    width: 100%;

`
export const navbar_moving_container = styled.div`
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: space-between;
    
    ${props => {
        if (props.navbar_magic) {
            return (
            `
            display: flex;
            height: 70px;
            position: fixed;
            background-color: #6200EE;
            max-width: 1000px;
            top: 1%;
            border-radius: 50px;
            box-shadow: 0px 2px 2px 1px rgba(13, 2, 18, 0.2);
            left: 50%;
            transform: translate(-50%, 0);
            transition: background-color ease 0.2s;
            `
            );
        }
    }}
    
`
// -------- LOGO ----------------
export const logo_container1 = styled.div`
    position: relative;
    height: 100%;
    width: 150px;
    left: 5%;
`

export const logo_container = styled.div`
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    
    
`

export const logo = styled.h1`
    
    font-size: 35px;
    font-family: 'Dancing Script', cursive;

    color: #293856;
    letter-spacing: 2px;
    
    ${props => {
        if (props.navbar_magic) {
            return (
            `
            transition: all .1s ease-in-out;
            color: #fff;
            `
            );
        }
    }}
`

// ----------------ITEMS----------------
export const navbar_items_container = styled.div`
    display: flex;
    
    right: 30px;
    position: absolute;

    a{
        color: #293856;
        text-decoration: none;
    }
    a:hover, a:focus, a:active {
        color: black;
        text-decoration: none;
    }
    

    ${props => {
        if (props.navbar_magic) {
            return (
            `
            transition: all .2s ease-in-out;
            a{
                color: #ffffff;
                text-decoration: none;
            }
            a:hover, a:focus, a:active {
                color: #ffffff;
                text-decoration: none;
            }
            `
            );
        }
    }}

    @media (max-width: 830px) {
        display: none;
    } 
`
export const navbar_items = styled.h3`
    margin-right: 30px;
    font-family: 'Open Sans', sans-serif;
 

`

// -------- HAMBURGER ----------------
export const navbar_toggler = styled.div`
@media (max-width: 830px) {
    position: relative;
    height: 40px;
    width: 35px;
    right: 40px;



} 
`
export const navbar_burger = styled.div`
@media (max-width: 830px) {
    transition: all .2s ease-in-out;
    position: absolute;
    width: 100%;
    height: 6px;
    border-radius: 5px;
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 44%;
  

    &:before, &:after {
        content: '';
        position: absolute;
        width: 100%;
        height: 6px;
        border-radius: 5px;
        background-color: black;
    };
    &:before {
        top: -10px;
    };
    &:after {
        top: 10px;
    };

    ${props => {
        if (props.navbar_magic) {
            return (
            `
            
            transform: translateX(-50px);
            background: transparent;
            

            &:before {
                transform: rotate(45deg) translate(35px, -35px);
            }
            &:after {
                transform: rotate(-45deg) translate(55px, 15px);
            }
            `
            );
        }
    }}
}
`